.input-text-container {
    width: 45%;
    border-radius: 5px;
    padding: 2%;
    box-shadow:.5px .5px 0.5px 1px;

  }
  
  .input-text-container textarea {
    width: 100%;
    height: 150px;
    padding: 10px;
    border-radius: 5px;
    border: none;
    background-color: black;
    margin-bottom: 10px;
    color: #ffff;
  }
  
  .input-text-container button {
    padding: 10px 20px;
    background-color: #61dafb;
    border: none;
    color: black;
    cursor: pointer;
    border-radius: 5px;
  }
  