.app {
  text-align: center;
  background-color: #282c34;
  color: white;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.content {
  display: flex;
  justify-content: space-between;
  width: 80%;
  margin: 20px 0;
}

.footer {
  margin-top: auto;
  padding: 10px;
  width: 100%;
  background-color: #20232a;
}
