.summary-container {
    width: 45%;
    border-radius: 5px;
    padding: 2%;
    box-shadow:.5px .5px 0.5px 1px;
  }
  
  .summary-container p {
    background-color: #20232a;
    padding: 20px;
    border-radius: 5px;
    border: 1px solid #61dafb;
  }

  .summary-container textarea{
    width: 100%;
    height: 150px;
    padding: 10px;
    border-radius: 5px;
    border: none;
    background-color: black;
    margin-bottom: 10px;
    color: #ffff;
  }
  